import { Component } from '@angular/core';
import { NavigationExtras } from '@angular/router';
import { ControllerService } from './service/controller.service';
import { Platform , NavController } from '@ionic/angular';
import { StorageService } from './service/storage.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {

  public selectedIndex = 0;

  public appPages = [
    {
      title: 'Home',
      url: '/tabs/tab3',
      icon: 'home'
    },
    {
      title: 'Profile',
      url: '/tabs/tab5',
      icon: 'person-circle'
    },
    {
      title: 'Cart',
      url: '/tabs/tab4',
      icon: 'cart'
    },
    {
      title: 'Wishlist',
      url: '/wishlist',
      icon: 'heart'
    },
    {
      title: 'Orders',
      url: '/tabs/tab2',
      icon: 'reader'
    },
    {
      title: 'Offers',
      url: '',
      icon: 'ticket'
    },
    {
      title: 'Product Overview',
      url: '/product-overview',
      icon: 'bag-handle'
    },
    {
      title: 'Contact Us',
      url: '',
      icon: 'call'
    },
    {
      title: 'Notification',
      url: '/notification',
      icon: 'notifications'
    },
    {
      title: 'Log Out',
      url: '/logout',
      icon: 'log-out'
    },
  ];

  constructor(
    public control: ControllerService,
    private plt: Platform,
    private navCtrl: NavController,
    private StorageService: StorageService,
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.plt.ready().then(() => {
      this.checkUserDetails();
    });
  }

  checkUserDetails() {
    this.StorageService.storage.get('isUserEnteredIntoHome').then((val) => {
      console.log('val:',val);
      if(val) {
        this.navCtrl.navigateRoot('/tabs/tab3');
      } else {
        this.navCtrl.navigateRoot('/landing');
      }
    }).catch((err) => {
      console.log('Error:',err);
    });
  }

  openPage(i: any) {
    const userid = 'PLJ73pTONAWPqptY6wSFFQn3jSA3';
    const navigationExtras: NavigationExtras = {
      state: {
        userId: userid
      }
    };
    const route = this.appPages[i].url;
    this.control.router.navigate([route], navigationExtras);
  }
}
